<template>
    <header class="index-head">
        <div class="index-head-left">
            <img src="../../assets/sheep_news.png" alt="">
            <ul>
                <li v-for="(title, index) in titles" :key="index" 
                @click="navTo(index)" :class="{active : index == isShow}">
                    <a href="#">{{title}}</a>
                </li> 
            </ul>
        </div>
        
        <div class="index-head-right">
            <!-- 登录前 -->
            <button @click="toLogin" v-if="!isLogin">一键登录</button>
            <button @click="toManage" v-else>管理中心</button>
        </div>

    </header>
</template>

<script>
export default {
    name: 'IndexHead',
    data() {
        return {
            titles: ['要闻','肺炎','北京','娱乐','体育','赛事','科技','财经'],
            isShow: 0,
            isLogin: true,
        }
    },
    methods: {
        toLogin() {
            this.$router.push('/login');
        },
        toManage() {
            this.$router.push('/manage');
        },
        // 点击导航栏 切换active并将vuex中的索引值改变
        navTo(index) {
            this.isShow = index;
            this.$store.state.newsAbout.newIndex = index;
        }
    },
    mounted() {
        // 判断显示登录还是管理中心
        this.isLogin = sessionStorage.getItem('allow');
    },
}
</script>

