<template>
    <article class="article">
        <div class="article-head">
            <h2>今日要闻</h2>
            <p>2021年8月30日 星期一</p>
        </div>
        
        <ul>
            <li v-for="(item, index) in newsList" :key="index">
                {{item}}
            </li>
        </ul>
    </article>
</template>

<script>
export default {
    name: 'Article',
    data() {
        return {
            newsList: [],
        }
    },
    // 将nav的索引设置为计算属性,当vuex中newIndex数据变化时 index也变化
    computed: {
        index() {
            return this.$store.state.newsAbout.newIndex
        }
    },
    // 初始化newsList
    mounted() {
        this.newsList = this.$store.state.newsAbout.news[this.index];
    },
    // 监视index 一旦变化 边更新newsList
    watch: {
        index() {
            this.newsList = this.$store.state.newsAbout.news[this.index];
        }
    }
}
</script>

