<template>
    <section class="section">
        <h2>热点精选</h2>

        <ul>
            <li v-for="(item, index) in hotList" :key="index">
                <img :src="item[0]" alt="">
                <div class="text">
                    <p>{{item[1]}}</p>
                    观察者网
                </div>
            </li>
        </ul>
        
    </section>
</template>

<script>
export default {
    name: 'Section',
    data() {
        return {
            hotList: [],
        }
    },
    // 将nav的索引设置为计算属性,当vuex中newIndex数据变化时 index也变化
    computed: {
        index() {
            return this.$store.state.newsAbout.newIndex
        }
    },
    // 初始化hotList
    mounted() {
        this.hotList = this.$store.state.newsAbout.hots[this.index];
    },
    // 监视index 一旦变化 边更新hotsList
    watch: {
        index() {
            this.hotList = this.$store.state.newsAbout.hots[this.index];
        }
    }
}
</script>

